import React, { useState, useEffect } from "react";
import { Redirect } from "react-router";
import axios from "axios";
import "./Changepassword.css"

export default function Changepassword({isAuth}) {

    let [state,setState] = useState({
        old: "",
        new: "",
        confirm: ""
    });
    let [msg,setMsg] = useState("");

    function handleChange(e){
        e.persist()
        setState(prev => {
            return {
                ...prev,
                [e.target.id]: e.target.value
            }
        });
        console.log(e.target);
    }

    function handleSubmit(){
        if(state.old==="" || state.new==="" ||state.confirm===""){
            setMsg("Feilds cannot be Empty");
        }
        else if(state.new !== state.confirm){
            setMsg("Confirmed password did not match");
        }
        else {
            const options = {
                method: 'post',
                url: '/logger/api/web/post/change_password',
                xsrfCookieName: 'csrftoken',
                xsrfHeaderName: 'X-CSRFTOKEN',
                data:{password:state.new,old:state.old,id:1},
                crossorigin:true,
                headers: {'Access-Control-Allow-Credentials': '*'}
            };
            axios.defaults.withCredentials = true    
            axios(options)
            .then(res=>{
                setMsg(res.data);
            })
            .catch(res=>{
                setMsg("Something went wrong try again later");
            });
        }
        setState({
            old: "",
            new: "",
            confirm: ""
        });
    }

    if(isAuth === false){
        return <Redirect to="/homepage/build" />
    }
    else {
        return (
            <div className="password-container">
                <label>Old Password</label>
                <input id="old" type="password" className="textarea" value={state.old} onChange={handleChange} />
                <label>New Password</label>
                <input id="new" type="password" className="textarea" value={state.new} onChange={handleChange} />
                <label>Confirm New Password</label>
                <input id="confirm" type="password" className="textarea" value={state.confirm} onChange={handleChange} /> 
                <button className="btn" onClick={handleSubmit}>Change</button>
                <div style={{color:msg=="Updated Successfully"?"green":"red"}}>*{msg}</div>
            </div>
        );
    }   
}