import React,{ useState, useEffect } from "react";
import axios from "axios";
import DjangoCSRFToken from 'django-react-csrftoken';
import './Workorder.css';

function Workorder({ workOrder,removeWorkOrder,employees }) {

    let [isExpanded,setExpanded] = useState(true);
    let [assign_to,setAssignTo] = useState("");

    function handleButtonClick(e){
        let employeeList = employees.map(employee => {
            return `${employee.emp_id} - ${employee.first_name} ${employee.last_name}`;
        })
        if(employeeList.includes(assign_to)){
            setExpanded(true);
            e.preventDefault();
            e.stopPropagation();
            const options = {
                method: 'post',
                url: '/logger/api/web/post/assign_to',
                xsrfCookieName: 'csrftoken',
                xsrfHeaderName: 'X-CSRFTOKEN',
                data:{assigned_to:assign_to,project_id:workOrder.woNumber},
                // withCredentials: false,
                crossorigin:true,
                headers: {'Access-Control-Allow-Credentials': '*'}
            };
            axios.defaults.withCredentials = true    
            axios(options)
            .then(res=>{
                removeWorkOrder();
                console.log(res.data);
            })
            .catch(res=>{
                console.log("Server Error");
            });
        }
        else{
            e.preventDefault();
            e.stopPropagation();
            alert("Employee does not exist");
            setAssignTo("");
        }
        
    }

    function handleShift(e) {
        setExpanded(true);
        e.stopPropagation();
        e.preventDefault();
    }

    function handleChange(e){
        setAssignTo(e.target.value);
    }

    return (
        <div onClick = {()=>{setExpanded(false);}} className={"work-order " + (isExpanded && "work-order-short")} >
            <h3>{workOrder.product_name}</h3>
            <div className="expand">
                <form action="">
                    <DjangoCSRFToken />
                    <label>WO Number: {workOrder.work_order_type+"-"+workOrder.woNumber}</label>
                    <label>Expected Date: {workOrder.expected_date}</label>
                    <label>Accepted By: {workOrder.accepted_by}</label>
                    <label>Customer Name: {workOrder.custtomer_name}</label>
                    <label>Customer Number: {workOrder.customer_contact}</label>
                    <label>Customer Email: {workOrder.customer_email}</label>
                    <label htmlFor="assign-to">Assign to:</label>
                    <input list="employees" autocomplete="off" name="assign-to" className="textarea textarea-inline" onChange={handleChange} value={assign_to} />
                    <button name="submit" className="btn btn-small btn-inline" onClick={handleButtonClick}>Assign</button>
                    <button name="submit" className="btn btn-small btn-inline" onClick={handleShift}>Later</button>

                    <datalist id="employees">
                        {employees.map(employee => {
                            return <option value={`${employee.emp_id} - ${employee.first_name} ${employee.last_name}`} />
                        })}
                    </datalist>
                </form>
            </div>
        </div>
    )
}

export default Workorder;