import React, { useState } from 'react';
import Navbar from "./components/NavBar/Navbar";
import Dashboard from './components/DashBoard/Dashboard';
import Signin from "./components/SignIn/Sigin";
import Projects from "./components/Projects/Projects";
import EmployeeContainer from "./components/Employee/EmployeeContainer";
import {Switch, Route, Redirect} from "react-router";
import {BrowserRouter} from "react-router-dom";
import Changepassword from './components/Changepassword/Changepassword';

function App() {
  let [isAuth,setAuth] = useState(false);

  return (
    <BrowserRouter>
      <div className="App">
        <Navbar isAuth={isAuth} setAuth={setAuth} />
        <Switch>
          <Route exact path="/homepage/build"><Signin isAuth={isAuth} setAuth={setAuth} /></Route>
          <Route path="/homepage/build/employees"><EmployeeContainer isAuth={isAuth} /></Route>
          <Route path="/homepage/build/projects"><Projects isAuth={isAuth}/></Route>
          <Route path="/homepage/build/dashboard"><Dashboard isAuth={isAuth} /></Route>
          <Route path="/homepage/build/change_password"><Changepassword isAuth={isAuth} /></Route>
          {/* <Redirect from="/" to="/build" /> */}
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
