import React, { useState,useEffect } from "react";
import Rates from "./Rates";
import axios from "axios";
import "./Rating.css"

function Rating({handleScrool}) {

    let [change,setChange] = useState(false);
    let [rates,setRatings] = useState([]);

    useEffect(()=>{
        axios.get("/logger/api/web/get/rating")
        .then((res)=>{
            let datas = JSON.parse(res.data);
            let activity = {};
            let arr = [];
            datas.forEach(data => {
                let fields = data.fields;
                let id = `${fields.emp_id}_${fields.date}`;
                if(activity.hasOwnProperty(id)){
                    activity[id].push(fields);
                }
                else{
                    activity[id] = [fields];
                }
            });
            for(let act in activity){
                arr.push(activity[act]);
            }
            setRatings(arr);
        })
        .catch(err => {
            console.log(err);
            setRatings([]);
        });
    },[change]);


    function removeRate(id,date){
        setRatings(rates.filter((rate)=>{
            return `${id}+${date}` !== `${rate.emp_id}+${rate.date}`; 
        }));
        setChange(!change);
    }

    return (
        <div>
            {rates.length !==0? 
                rates.map( (rate,index) => {
                    return (<Rates rate = {rate} key={index} id={index} removeRate={removeRate} handleScrool={handleScrool} />)
                }):
                <p className="no-rate">No Works to Rate</p>
            }
            
        </div>
    )
}

export default Rating;