import React, { useState, useEffect } from "react";
import Wordorder from "./WorkOrder/Wordorder";
import Rating from "./Rating/Rating";
import { Redirect } from "react-router";
import axios from "axios";
import "./Dashboard.css";

function Dashboard(props) {

    let [scrool,setScroll] = useState(true);
    let [change,setChange] = useState(false);
    let [workOrders,setWorkOrder] = useState([]);
    let [employees,setemployees] = useState([]);

    useEffect(()=>{
        let mounted = true;
        if(mounted){
            axios.get("/logger/api/web/get/wo_pending")
            .then((res)=>{
                let datas = JSON.parse(res.data);
                let values = datas.map((data)=>{
                    let pk = data.pk;
                    let fields = data.fields;
                    fields["woNumber"] = pk;
                    return fields;
                });
                setWorkOrder(values);
            })
            .catch((res)=>{
                //setWorkOrder([]);
                console.log("ERROR");
            });

            axios.get("/logger/api/web/get/employee")
            .then((res)=>{
                let datas = JSON.parse(res.data);
                let values = datas.map((data)=>{
                    let pk = data.pk;
                    let fields = data.fields;
                    fields["empid"] = pk;
                    return fields;
                });
                setemployees(values);
            })
            .catch((res)=>{
                //setWorkOrder([]);
                console.log("ERROR" + res);
            });

        }
        
        return ()=> mounted = false;
        
    },[change]);

    function removeWorkOrder() {
        setChange(prev => !prev);
    }

    function handleScrool(bool){
        if(bool){
           setScroll(false); 
        }
        else{
           setScroll(true);
        }
    }

    if(props.isAuth === false){
        return <Redirect to="/homepage/build" />
    }
    else{
        return (
            <section className={"dashboard-container " + (!scrool && "scroll-hidden")}>
                <div className="work-order-container">
                    <h1>Work Orders Pending:</h1>
                    {
                    workOrders.length !== 0?
                        workOrders.map((workOrder,id) => {
                            return <Wordorder 
                                        workOrder={workOrder} 
                                        removeWorkOrder={removeWorkOrder} 
                                        key={workOrder.woNumber}
                                        employees={employees} />
                        }):
                        <h3>No Work order pending refresh for updates</h3>
                    }
                </div>
                <div className="rating-container">
                    <h1>Rate Works</h1>
                    <Rating handleScrool={handleScrool}/>
                </div>
            </section>
        )
    }
}

export default Dashboard;