import React, { useState,useEffect } from "react";
import Employee from "./Employee";
import EmployeeDetails from "./EmployeeDetails";
import Addemployee from "./Addemployee";
import { Redirect } from "react-router";
import axios from "axios";
import "./EmployeeContainer.css"

function EmpolyeeContainer({ isAuth }) {
    let [isEmployeeSelected,setEmployeeSelected] = useState(false);
    let [details,setDetails] = useState(null);
    let [employees,setemployees] = useState([]);
    let [change,setChange] = useState(true);
    let [isAddVisible,setAddVisible] = useState(false);

    useEffect(()=>{
        let mounted = true;
        if(mounted){
            axios.get("/logger/api/web/get/employee")
            .then((res)=>{
                let datas = JSON.parse(res.data);
                let values = datas.map((data)=>{
                    let pk = data.pk;
                    let fields = data.fields;
                    fields["empid"] = pk;
                    return fields;
                });
                setemployees(values);
            })
            .catch((res)=>{
                //setWorkOrder([]);
                console.log("ERROR" + res);
            });
        }
        
        return ()=> mounted = false;
        
    },[change]);
    
    function handleDisplay(id) {
        setEmployeeSelected(true);
        let val = employees.find((employee)=>employee.empid===id);
        setDetails(val);
    }

    function returnBack(){
        setEmployeeSelected(false);
    }

    function handleAdd() {
        axios.get("/logger/api/web/get/add_employee")
        .then((res) => console.log(res.data));
    }

    return isAuth? isEmployeeSelected ? 
           (<EmployeeDetails details={details} returnBack={returnBack}/>) : 
           (
            <div className="employee-container-outer">
                <h1 style={{display:"flex",alignItems:"center"}}>Employees: 
                    <button className="btn btn-inline btn-small" onClick={() => setAddVisible(true)}>ADD</button>
                </h1>
                <div className="employee-container">
                    {employees.map(employee=>{
                        return <Employee first_name={employee.first_name} 
                                        last_name={employee.last_name} 
                                        displayEmployee = { handleDisplay } 
                                        id={employee.empid} key={employee.empid}
                                        setChange={setChange} />
                    })}
                </div>
                {isAddVisible && <Addemployee setAddVisible={setAddVisible} handleAdd={handleAdd} />}
            </div>
            ):
            <Redirect to="/homepage/build" />
}

export default EmpolyeeContainer;