import React from "react";
import "./Prompt.css"

export default function Prompt({msg,handleDelete,setPromptVisible}) {
    return (
        <div class="prompt-container">
            <div class="prompt">
                <p>{msg}</p>
                <button class="btn btn-inline btn-small" onClick={(e) => {setPromptVisible(false);e.stopPropagation()}}>Cancel</button>
                <button class="btn btn-inline btn-small" onClick={(e) => {handleDelete();e.stopPropagation()}}>Yes</button>
            </div>
            <div class="overlay overlay-visible"></div>
        </div>
    ) 
}