import React, { useState } from "react";
import Getrate from "./Getrate";
import "./Rates.css";


function Rates({ rate,removeRate,handleScrool }){

    let [isClicked,setClicked] = useState({
        bool: false
    });

    function clickSetter(bool,isDone){
        if(bool === false){
            setClicked({
                bool: false
            });  
        }
        if(isDone === true){
            removeRate(rate[0].emp_id,rate[0].date);
        }
        handleScrool(false);
    }

    return ( 
        <div>
            <div className="rate" onClick={()=>{setClicked({bool:true});handleScrool(true)}}>
                <h4>Rate: {rate[0].emp_id}</h4>
            </div>
            {isClicked.bool && <Getrate setClicked={ clickSetter } rateArray={ rate }/>}
        </div>
     );
}

export default Rates;