import React, { useEffect, useState } from "react";
import axios from "axios";
import "./Addemployee.css";

export default function Addemployee ({handleAdd,setAddVisible}) {

    let [avail,setAvail] = useState([]);
    let [change,setChange] = useState(true);
    
    useEffect(() => {
        axios.get("/logger/api/web/get/get_id")
        .then(res => {
            let ids = JSON.parse(res.data);
            let arr = ids.map(id => {
                return id.pk;
            })
            setAvail(arr);
        });
    },[change]);

    return (
        <div className="add-container">
            <div className="add">
                <h3>Available ID</h3>
                {avail.map(item => {
                    return (<p key={item}>{item}</p>);
                })}
               <button className="btn btn-inline btn-small" onClick={(e) => {setAddVisible(false);e.stopPropagation()}}>Cancel</button>
                <button className="btn btn-inline btn-small" onClick={(e) => {handleAdd();setChange(prev => !prev);e.stopPropagation()}}>Yes</button>
            </div>
            <div className="overlay overlay-visible"></div>
        </div>
    )
}