import React, { useEffect, useState } from "react";
import axios from "axios";
import "./EmployeeDetails.css"

function EmployeeDetails({ details,returnBack }) {

    let [work,setWork] = useState([]);

    useEffect(()=>{
        let d = new Date();
        axios.get("http://127.0.0.1:8000/logger/api/web/get/employee_work?id=" + details.empid + `&date=${d.getFullYear()}-${d.getMonth()+1}-${d.getDate()}`)
        .then(res=>{
            let datas = JSON.parse(res.data);
            let values = datas.map((data)=>{
                let pk = data.pk;
                let fields = data.fields;
                fields["id"] = pk;
                return fields;
            });
            console.log(values);
            setWork(values);
        })
        .catch(res=>{
            console.log("ERROR" + res);
        });
    },[]);


    return (
        <div className="employee-details">
            <div className="employee-division">
                <h2>Employee Details</h2>
                <p><span>Name: </span>{details.first_name +" "+details.last_name}</p>
                <p><span>Id: </span>{details.empid}</p>
                <p><span>E-mail: </span>{details.email}</p>
                <p><span>Phone Number: </span>{details.phone_no}</p>
                <p><span>Educational Qualification: </span>{details.educational_qualification}</p>
                <p><span>Date of joining: </span>{details.date_of_joining}</p>
                <p><span>Residence: </span><br />{details.street}<br />{details.district}<br />{details.state}</p>
                <button className="btn" onClick={()=>{returnBack()}}>Done</button>
            </div>
            <div className="employee-work-table ">
                <h2>Current Activity</h2>
                <table className="table ">
                    <thead>
                        <tr>
                            <th>Activity</th>
                            <th>Planned time</th>
                            <th>Remarks</th>
                        </tr>
                    </thead>
                    <tbody>
                        {work.map(individual=>{
                            return (<tr>
                                        <td>{individual.activity}</td>
                                        <td>{individual.planned_time}</td>
                                        <td>{individual.work_remark}</td>
                                    </tr>)
                        })}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default EmployeeDetails;