import React, { useState } from "react";
import icon from "../../icon.jpg";
import { Link } from "react-router-dom";
import Settings from "./Settings/Settings";
import './Navbar.css';

function Navbar(props) {

    let [isSettings,setSettings] = useState(0);

    return (
        <header className = "nav-bar">
            <Link to="/homepage/build/dashboard"><img src={icon} alt="logo" className="logo" /></Link>
            { props.isAuth && <div className="links">
                <Link to="/homepage/build" onClick={()=>{props.setAuth(false)}}>Log out</Link>
                <div className="avatar" onClick={(e)=>{setSettings(1)}}></div>
            </div> }
            
            <Settings isSettings={isSettings} setSettings={setSettings} />
        </header>
    )
}

export default Navbar;