import React, { useState } from "react";
import "./Getrate.css";
import axios from "axios";

function Getrate({ setClicked,rateArray }) {

    let [rating,setRate] = useState("");
    console.log(rating);

    function rate(){
        if(rating<=100 && rating>=0){
            const options = {
                method: 'post',
                url: '/logger/api/web/post/manager_rating',
                xsrfCookieName: 'csrftoken',
                xsrfHeaderName: 'X-CSRFTOKEN',
                data:{rating:parseInt(rating),date:rateArray[0].date,emp_id:rateArray[0].emp_id},
                // withCredentials: false,
                crossorigin:true,
                headers: {'Access-Control-Allow-Credentials': '*'}
            };
            axios.defaults.withCredentials = true    
            axios(options)
            .then(res=>{
                setClicked(false,true);
            })
            .catch(res=>{
                console.log("Server Error");
            });
        }
        else{
            alert("Rating must be between 0-100");
            setRate("");
        }
    }

    return (
        <div className="get-rate-container">
            <div className="get-rate">
                <h3>Activities of {rateArray[0].emp_id} on {rateArray[0].date}</h3>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Activity</th>
                            <th>WO Number</th>
                            <th>Planned time</th>
                            <th>Actual time</th>
                            <th>Work Remark</th>
                            <th>Submission Remark</th>
                            <th>Dependency</th>
                        </tr>
                    </thead>
                    <tbody>
                {rateArray.map(rate => {
                    console.log(rate);
                    return(<tr>
                        <td>{rate.activity}</td>
                        <td>{rate.work_order_no}</td>
                        <td>{rate.planned_time}</td>
                        <td>{rate.actual_time}</td>
                        <td>{rate.work_remark}</td>
                        <td>{rate.submission_remark}</td>
                        <td>{rate.dependency}</td>
                    </tr>)
                })}
                </tbody>
                </table>
                <input 
                    type="text" 
                    className="textarea textarea-inline" 
                    value={rating} 
                    placeholder="Out of 100" 
                    onChange={(e) => setRate(e.target.value)}
                    min="0"
                    max="100" />
                <button className="btn btn-small btn-inline" 
                        onClick={()=>{rate()}}>
                        Rated
                </button>
            </div>
            <div className="overlay overlay-visible" onClick={()=>{setClicked(false,false)}}></div>
        </div>
    )
}

export default Getrate;