import React,{ useState,useEffect } from "react";
import Project from "./Project";
import { Redirect } from "react-router";
import axios from "axios";
import "./Projects.css"

function Projects({ isAuth }) {
    
    let [workOrders,setWorkOrder] = useState([]);
    
    useEffect(()=>{
        axios.get("/logger/api/web/get/current_projects")
        .then((res)=>{
            let datas = JSON.parse(res.data);
            let values = datas.map((data)=>{
                let pk = data.pk;
                let fields = data.fields;
                fields["woNumber"] = pk;
                return fields;
            });
            setWorkOrder(values);
        })
    },[]);

    if(isAuth === false){
        return <Redirect to="/homepage/build" />
    }

    else{
        return(
            <div className="projects">
                <h1>Projects</h1>
                {workOrders.length?
                workOrders.map(workOrder => {
                    return <Project workOrder={workOrder} />
                }):
                <h3>No Projects to display refresh for updates</h3>
                }
            </div>
        );
    }
}

export default Projects;