import React,{useState} from "react";
import axios from "axios";
import Prompt from "./Prompt";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons'
import "./Employee.css"

function Employee({ img,first_name,last_name,displayEmployee,id,setChange }){

    let [isPromptVisible,setPromptVisible] = useState(false);

    function handleDelete(e) {
        const options = {
            method: 'post',
            url: '/logger/api/web/post/delete_employee',
            xsrfCookieName: 'csrftoken',
            xsrfHeaderName: 'X-CSRFTOKEN',
            data:{emp_id:id},
            // withCredentials: false,
            crossorigin:true,
            headers: {'Access-Control-Allow-Credentials': '*'}
        };
        axios.defaults.withCredentials = true;    
        axios(options)
        .then(res => {
            console.log(res.data)
            setChange(prev => !prev);
        });
    }

    return (
        <div className="employee" onClick={()=>{displayEmployee(id)}}>
            <div className="employee-avatar">
                <img src=""></img>
            </div>
            <p>{first_name} {last_name}</p>
            <div className="employee-delete" onClick={(e) => {setPromptVisible(true);e.stopPropagation()}}>
                <FontAwesomeIcon icon={faTrashAlt} />
            </div>
            {isPromptVisible && <Prompt msg="Are you sure you want to delete this employee?" handleDelete={handleDelete} setPromptVisible={setPromptVisible}/>}
        </div>
    )
}

export default Employee;