import React, { useState, useEffect } from "react";
import { Link,Redirect } from "react-router-dom";
import axios from "axios";
import './Signin.css';

function SignIn(props) {
    
    let [state,setState] = useState({
        password: ""
    });

    function handleChange(e) {
        setState({
            ...state,
            [e.target.name]: e.target.value
        });
    }

    function handleSubmit(e) {
        const options = {
            method: 'post',
            url: '/logger/api/web/post/authenticate',
            xsrfCookieName: 'csrftoken',
            xsrfHeaderName: 'X-CSRFTOKEN',
            data:{id:1,password:state.password},
            crossorigin:true,
            headers: {'Access-Control-Allow-Credentials': '*'}
        };
        axios.defaults.withCredentials = true;    
        axios(options)
        .then(res => {
            if(res.data === "Success"){
                props.setAuth(true);
            }
        });
    }

    if(props.isAuth === true) {
        return <Redirect to="/homepage/build/dashboard" />
    }
    
    else{
        return ( 
            <section className="login-container" >
                <h1>Welcome</h1>
                <form action="" className="log-in">
                    <label htmlFor="password">Password</label>
                    <input type="password" name="password" autoComplete="off" className="textarea" value={state.password} onChange={handleChange} />
                    <Link to="/homepage/build/dashboard"><button className="btn" onClick={handleSubmit}>Log in</button></Link>
                </form>
            </section>
        );
    }
}

export default SignIn;