import React from "react";
import { Link } from "react-router-dom";
import "./Settings.css";

function Settings({ isSettings,setSettings }) {
    return(
        <div>
            <section className={"setting " + (isSettings && "setting-visible")} >
                    <div className="setting-option">
                    </div>
                    <div className="setting-option" onClick={()=>{setSettings(0)}}>
                        <Link to="/homepage/build/dashboard">Home</Link>
                    </div>
                    <div className="setting-option" onClick={()=>{setSettings(0)}}>
                        <Link to="/homepage/build/projects">Project Details</Link>
                    </div>
                    <div className="setting-option" onClick={()=>{setSettings(0)}}>
                        <Link to="/homepage/build/employees" >Employee</Link>
                    </div>
                    <div className="setting-option" onClick={()=>{setSettings(0)}}>
                        <Link to="/homepage/build/change_password">Change Password</Link>
                    </div>
            </section>
            <div className={"overlay " + (isSettings && "overlay-visible")} onClick={()=>{setSettings(0)}}></div>
        </div>
    )
}

export default Settings;